<script>
export default {
    name:'subjectApplyEndShow',
    data(){
        return{
            showForm:{
                topicName:'',
                topicNumber:'',
                subjectUndertakingUnit:'',
                unitNature:'',
                projectLeader:'',
                projectLeaderPhone:'',
                projectLeaderMail:'',
                postalAddress:'',
                agreementStartTime:'',
                agreementEndTime:'',
                applicationAcceptanceTime:'',
                applicationAcceptanceMode:[],
                applicationAcceptancePlace:'',
                acceptanceContact:'',
                acceptanceContactPhone:'',
                mainContentSituation:'',
                submissionAchievementsSituation:'',
                submitInventory:'',
                subjectUndertakingUnitOpinion:'',
                environmentalDepartmentsOpinion:'',
                provinceAssessmentCenterOpinion:'',
                competentDepartmentOinion:'',
                applicationAcceptanceUrl:'',
                achievementsUrl:'',
                extranetCheckApplyStateList:[{
                    auditStep:'',
                    checkApplyId:'',
                    firstHandleTime:'',
                    fistHandler:'',
                    handleContent:'',
                    id:'',
                    secondHandleTime:'',
                    secondHandler:'',
                    state:''
                }],
                acceptanceCertificate:{
                    // 
                    acceptanceCertificatePatentList:[{
                        applicationInvention:'',
                        useNewType:'',
                        patentAppearance:'',
                        patentInvention:'',
                        empowerNewType:'',
                        empowerAppearanceDesign:'',
                        paperNumber:'',
                        scienceIndexe:'',
                        engineerIndex:'',
                        publishWork:'',
                        technicalStandard:'',
                        newProduct:'',
                        policySystem:'',
                        newDevice:'',
                        newTechnology:''
                    }],
                    // 
                    acceptanceCertificateSubjectPeopleList:[{
                        name:'',
                        sex:'',
                        birthDate:'',
                        major:'',
                        education:'',
                        title:'',
                        phone:''
                    }],
                    // 
                    acceptanceCertificatePrincipalPersonnelList:[{
                        participantName:'',
                        participantSex:'',
                        participantBirthDate:'',
                        participantTechnicalTitle:'',
                        participantEducation:'',
                        participantWorkUnit:'',
                        taskTaking:''
                    }],
                }
            },
            unitNatureOptions:[{//
                "id": 1,
                "classification": null,
                "content": "",
                "classificationId": null,
                "contentId": null,
                "state": null
            }],
            applicationAcceptanceModeOptions:[{//
                id:'',
                content:''
            }],
            submitInventoryOptions:[{//
                id:'',
                content:''
            }],
            paramsData: {
                id: this.$route.params.id,
                arrays:this.$route.params.arrays
            },
            finalAcceptanceMethod:[{
                id:'',
                content:''
            }],
            // 
            achievementFormOptions:[{
                id:'',
                content:'',
            }],
            // 
            achievementLevelOptions:[{
                id:'',
                content:''
            }],
            sexOptions:[{
                value:'1',
                label:''
            },{
                value:'2',
                label:''
            }],
        }
    },
    methods:{

        // 
        handleBack(){
            // this.$router.go(-1);//
            this.$router.back(-1)
        },
        // 
        handleConfirm(){
            let specialAuditFile=document.getElementById('specialAuditFile').files[0]
            let firstInspectionFile=document.getElementById('firstInspectionFile').files[0]
            if(specialAuditFile == undefined || firstInspectionFile == undefined){
                this.$alert('', '', {
                    confirmButtonText: '',
                    type:'warning',
                }).then(() => {
                }).catch(() => {
                })
                return
            }
            this.submitFiles(specialAuditFile,firstInspectionFile)
        },
        // 
        submitFiles(specialAuditFile,firstInspectionFile){
            let _this = this
            let _id = _this.paramsData.id
            let formData = new FormData()
            formData.append('id',_id)
            formData.append('type',true)
            formData.append('specialAuditFile',specialAuditFile)
            formData.append('firstInspectionFile',firstInspectionFile)
            _this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/acceptState/examine',
                data:formData
            }).then(function(res){
                if(res.resultFlag == 1){
                    _this.$alert(res.data.data, '', {
                        confirmButtonText: '',
                        type:'warning',
                    }).then(() => {
                        _this.$router.push('/')
                    }).catch(() => {
                        _this.$router.push('/')
                    })
                }else{
                    _this.$alert('', '', {
                        confirmButtonText: '',
                        type:'warning',
                    }).then(() => {
                        _this.$router.back(0)
                    }).catch(() => {
                        _this.$router.back(0)
                    })
                }
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getUnitNature(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/unitNature',
            }).then(function(res){
                _this.unitNatureOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        //
        getApplicationAcceptanceModeOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationAcceptance',
            }).then(function(res){
                _this.applicationAcceptanceModeOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getSubmitInventoryOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationSubmitList'
            }).then(function(res){
                _this.submitInventoryOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // id
        TicketParams(id,arrays) {
            if(arrays != undefined){
                arrays.map((item) => {
                    if(id == item.id){
                        this.showForm = item
                        return;
                    }
                })
            }
        },
        // 
        getAcceptance(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/finalAcceptanceMethod',
            }).then(function(res){
                _this.finalAcceptanceMethod = res.data.data
                console.log(res)
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getAchievementFormOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/queryAchievementShape',
            }).then(function(res){
                _this.achievementFormOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getAchievementLevelOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/queryAchievementLevel',
            }).then(function(res){
                _this.achievementLevelOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        }

    },
    async mounted(){
       await this.getUnitNature()//
       await this.getApplicationAcceptanceModeOptions()//
       await this.getSubmitInventoryOptions()//
       await this.getAchievementFormOptions()//
       await this.getAchievementLevelOptions()//
       await this.TicketParams(this.paramsData.id,this.paramsData.arrays)
       await this.getAcceptance()
    }
}
</script>
<style lang="less">
#subjectApplyEndShow{
    .showForm{
        .el-form{
            p,h1{
                padding-left: 40px;
                margin: 15px auto;
                text-align: left;
                width: 59.4%;
                min-width: 900px;
                font-weight: bold;
                font-size: 18px;
            }
            h1{
                margin: 15px auto;
                font-size: 23px;
                padding-left: 20px;
            }
            th{
                .el-input{
                    display: inline-block;
                    width: auto;
                    height: 50px;
                    line-height: 50px;
                }
            }
            .el-table{
                min-width: 900px;
            }
            .form_table,.form_table1,.form_table2{
                min-width: 900px;
                td{
                    height: 50px;
                }
                .textalignCenter{
                    td{
                        text-align: center;
                    }
                }
            }
            .form_table1{
                position: relative;
                tr{
                    position: relative;
                    td{
                        text-align: right;
                        .el-input__inner{
                            text-align: left;
                        }
                    }
                }
            }
            .form_table3{
                tr{
                    td:first-child,
                    td:nth-child(3),
                    td:nth-child(5){
                        background-color: #e5f3ff;
                    }
                }
            }
            table.form_table2 {
                tbody {
                    tr{
                        td:first-child{
                            text-align: right;
                            padding-right: 20px;
                        }
                    }
                    tr:first-child td{
                        border: 1px solid #e0e0e0;
                    }
                }
            }
            
            .el-select{
                .el-input__suffix{
                    line-height: 50px;
                }
            }
            .patent{
                text-align: left;
                .el-input{
                    width: 79%;
                }
            }
            .el-date-editor{
                width: 100%;
                line-height:50px;
                span{
                    line-height: 50px;
                }
            }
            .el-checkbox-group,
            .el-radio-group.level{
                text-align: left;
                font-size: 0;
                line-height: 1;
                height: 100%;
                width: 100%;
                >span{
                    min-width: 200px;
                    line-height: 1;
                    padding:5px 10px;
                    .el-checkbox{
                        span{
                            line-height: 1;
                            &:last-child{
                                padding-left: 5px;
                            }
                        }
                    }
                     .el-radio{
                        line-height: 1;
                        margin: 0;
                     }
                }
            }
            .audit_process{
                margin: 40px auto;
                width: 70%;
                min-width: 100px;
                tr{
                    td{
                        border-left: 1px solid #e0e0e0;
                        border-bottom: 1px solid #e0e0e0;
                        height: 50px;
                        text-align: center;
                        &:last-child{
                            border-right: 1px solid #e0e0e0;
                        }
                    }
                    &:first-child{
                        border-top: 1px solid #e0e0e0;
                        td{
                            background-color:#e5f3ff;
                        }
                    }
                }
            }
            td{
                text-align: left;
            }
            .el-select{
                width: 100%;
            }
            .el-radio{
                line-height: 50px;
                margin: 0 30px;
                .el-radio__input{
                    padding-right: 5px;
                }
                .el-radio__input.is-disabled+span.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked+.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked .el-radio__inner{
                    background-color: #0abd90;
                    border-color: #0abd90;
                }
                .el-radio__input.is-disabled.is-checked .el-radio__inner::after{
                    background-color: #fff;
                }
            }
            textarea{
                min-height: 100px !important;
                padding: 0 10px;
            }
            .el-checkbox-group{
                >span{
                    margin:5px 10px;
                    display: inline-block;
                    .el-checkbox{
                        min-width: 120px;
                        .el-checkbox__label{
                            display: inline-block;
                        }
                    } 
                }
            }
            .el-checkbox__label{
                margin-left: 5px;
            }
            #achievementsFile,#applicationAcceptanceFile,#submitInventoryFile{
                margin-left:10px;
            }
            .el-date-editor{
                .el-input__inner{
                    padding-left: 30px;
                }
                .el-input__prefix{
                    line-height: 50px;
                }
            }
        }
    }
    .el-dialog__wrapper{
        .el-dialog{
            margin-top: 0 !important;
            .el-dialog__body{
                .button{
                    text-align: center;
                    .el-button{
                        display:inline-block;
                        margin: auto;
                    }
                }
            }
        }
        .el-form{
            .el-form-item__label{
                width: 277px !important;
            }
            .el-form-item__content{
                margin-left: 277px !important;
            }
            textarea{
                min-height: 278px !important;
                padding: 10px;
            }
            
        }
        
        .el-dialog__footer{
            text-align: center;
        }
    }
}

</style>



