<script>
export default {
    name:'subjectUploadSecond',
    data(){
        return{
            showForm:{
                translate:'1',
                topicName:'1',
                topicNumber:'1',
                completionUnit:'1',
                acceptanceDepartment:'',
                acceptanceTime:'',
                unitName:'1',
                unitNature:'1',
                location:'1',
                legalRepresentative:'1',
                legalRepresentativePhone:'1',
                contacts:'1',
                contactsPhone:'1',
                postalCode:'1',
                mail:'1',
                mailingAddress:'1',
                competentDepartment:'1',
                projectStartTime:'',
                projectCompletionTime:'',
                achievementForm:[],
                achievementLevel:'1',
                developmentTotalNumber:0,
                doctorTotalNumber:'1',
                masterTotalNumber:'1',
                seniorTotalNumber:'1',
                intermediateTotalNumber:'1',
                schoolMasterNumber:'1',
                totalProjectFunds:'1',
                environmentTopicFunds:'1',
                competentDepartmentMatch:'1',
                bankLoans:'1',
                unitRaiseMoney:'1',
                otherActualMoney:'1',
                equipmentCost:'1',
                materialFee:'1',
                laboratoryFees:'1',
                fuelCosts:'1',
                travelExpenses:'1',
                conferenceFee:'1',
                internationalCommunication:'1',
                expertConsult:'1',
                managementExpense:'1',
                otherExpenditureMoney:'1',
                totalExpenditure:0,
                newOutput:'1',
                newSalesVolume:'1',
                newProfitTax:'1',
                exitEarn:'1',
                mainSolveTechnology:'1',
                mainCompletion:'1',
                implementationAchievement:'1',
                scienceDepartmentOpinion:'1',
                checkDepartmentOpinion:'1',
                environmentOfficeOpinion:'1',
                // 
                acceptanceCertificatePatentList:[{
                    applicationInvention:'1',
                    useNewType:'1',
                    patentAppearance:'1',
                    patentInvention:'1',
                    empowerNewType:'1',
                    empowerAppearanceDesign:'1',
                    paperNumber:'1',
                    scienceIndexe:'1',
                    engineerIndex:'1',
                    publishWork:'1',
                    technicalStandard:'1',
                    newProduct:'1',
                    policySystem:'1',
                    newDevice:'1',
                    newTechnology:'1'
                }],
                // 
                acceptanceCertificateSubjectPeopleList:[{
                    name:'1',
                    sex:'1',
                    birthDate:'',
                    major:'1',
                    education:'1',
                    title:'1',
                    phone:'1'
                }],
                // 
                acceptanceCertificatePrincipalPersonnelList:[{
                    participantName:'1',
                    participantSex:'1',
                    participantBirthDate:'',
                    participantTechnicalTitle:'1',
                    participantEducation:'1',
                    participantWorkUnit:'1',
                    taskTaking:'1'
                }],
            },
            // 
            unitNatureOptions:[{
                id:'',
                content:''
            }],
            // 
            achievementFormOptions:[{
                id:'',
                content:'',
            }],
            // 
            achievementLevelOptions:[{
                id:'',
                content:''
            }],
            sexOptions:[{
                value:'1',
                label:''
            },{
                value:'2',
                label:''
            }],
            params:{
                id:this.$route.params.id,
                arrays:this.$route.params.arrays
            },
            title1:'',
            title2:''
        }
    },
    methods:{
        // 
        handleSubmit(){
            for(let i in this.showForm) {
                if(typeof(this.showForm[i]) == "string" ) {
                    if(this.showForm[i].match(/^[ ]*$/) || this.showForm[i] == undefined){
                        this.$alert('', '', {
                            confirmButtonText: '',
                            type: 'warning',
                            callback: action => {}
                        });
                        return false;
                    }
                }
                if(typeof(this.showForm[i]) == "object") {
                    if(this.showForm[i].length == 0) {
                        this.$alert('', '', {
                            confirmButtonText: '',
                            callback: action => {}
                        });
                        return false;
                    }
                }
            }
            let $lastReport = document.getElementById('lastReport').files[0]
            if($lastReport == undefined){
                this.$alert('', '', {
                    confirmButtonText: '',
                    callback: action => {}
                });
                return
            }
            this.submitAxios($lastReport)
        },
        submitAxios(lastReport){
            let _this = this
            console.log(_this.showForm.acceptanceCertificatePatentList)
            let _achievementForm = JSON.stringify(_this.showForm.achievementForm)
            _this.showForm.achievementForm = _achievementForm
            var formData = new FormData()
            formData.append('caId',_this.params.id)
            formData.append('lastReport',lastReport)
            let acceptanceCertificate = JSON.stringify(_this.showForm)
            formData.append('acceptanceCertificate',new Blob([acceptanceCertificate],{type:'application/json'}))
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/apply/submitLastReport',
                data:formData
            }).then(function(res){
                let data = ''
                if(res.data.resultFlag == 0){//
                   data = res.data.data
                }
                if(res.data.resultFlag == 1){
                   data = res.data.data
                }
                _this.$alert(data, '', {
                    confirmButtonText: '',
                    type:'warning',
                }).then(() => {
                    _this.$router.back(0)
                }).catch(() => {
                    _this.$router.back(0)
                })
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        handleBack(){
            this.$router.back(-1)
        },
        // 
        getShowForm(formData){
            if(formData != undefined){
                formData.map((item) => {
                    if(this.params.id == item.id){
                        this.showForm.topicName = item.topicName
                        this.showForm.topicNumber = item.topicNumber
                        this.showForm.projectLeader = item.projectLeader
                        this.showForm.subjectUndertakingUnit = item.subjectUndertakingUnit
                        return;
                    }
                })
            }
            
        },
        // 
        getUnitNaturesOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/unitNature'
            }).then(function(res){
                _this.unitNatureOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getAchievementFormOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/queryAchievementShape'
            }).then(function(res){
                _this.achievementFormOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getAchievementLevelOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/queryAchievementLevel'
            }).then(function(res){
                _this.achievementLevelOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        }
    },
    async created(){
        await this.getShowForm(this.params.arrays)
        await this.getUnitNaturesOptions()
        await this.getAchievementFormOptions()
        await this.getAchievementLevelOptions()
    }
}
</script>
<style lang="less">
#subjectUploadSecond{
    .showForm{
        .el-form{
            p,h1{
                padding-left: 40px;
                margin: 15px auto;
                text-align: left;
                width: 59.4%;
                min-width: 900px;
                font-weight: bold;
                font-size: 18px;
            }
            h1{
                margin: 15px auto;
                font-size: 23px;
                padding-left: 20px;
            }
            th{
                .el-input{
                    display: inline-block;
                    width: auto;
                    height: 50px;
                    line-height: 50px;
                }
            }
            .form_table1,.form_table,.form_table2{
                min-width: 900px;
                td{
                    height: 50px;
                }
                .textalignCenter{
                    td{
                        text-align: center;
                    }
                }
            }
            .form_table1{
                position: relative;
                tr{
                    position: relative;
                    td{
                        text-align: right;
                        .el-input__inner{
                            text-align: left;
                        }
                    }
                }
            }
            .form_table3{
                tr{
                    td:first-child,
                    td:nth-child(3),
                    td:nth-child(5){
                        background-color: #e5f3ff;
                    }
                }
            }
            table.form_table2 {
                tbody {
                    tr{
                        td:first-child{
                            text-align: right;
                            padding-right: 20px;
                        }
                    }
                    tr:first-child td{
                        border: 1px solid #e0e0e0;
                    }
                }
            }
            .el-select{
                .el-input__suffix{
                    line-height: 50px;
                }
            }
            .patent{
                text-align: left;
                .el-input{
                    width: 79%;
                }
            }
            .el-date-editor{
                width: 100%;
                line-height:50px;
                span{
                    line-height: 50px;
                }
            }
            .el-checkbox-group,
            .el-radio-group.level{
                text-align: left;
                font-size: 0;
                line-height: 1;
                height: 100%;
                width: 100%;
                >span{
                    min-width: 200px;
                    line-height: 1;
                    padding:5px 10px;
                    .el-checkbox{
                        span{
                            line-height: 1;
                            &:last-child{
                                padding-left: 5px;
                            }
                        }
                    }
                     .el-radio{
                        line-height: 1;
                        margin: 0;
                     }
                }
            }
            .el-radio{
                line-height: 50px;
                margin: 0 30px;
                .el-radio__input{
                    padding-right: 5px;
                }
                .el-radio__input.is-disabled+span.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked+.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked .el-radio__inner{
                    background-color: #0abd90;
                    border-color: #0abd90;
                }
                .el-radio__input.is-disabled.is-checked .el-radio__inner::after{
                    background-color: #fff;
                }
            }
            textarea{
                width: 100%;
                height: 100%;
                padding: 10px;
            }
            // .addCircle{
            //     position: absolute;
            //     top: 60px;
            //     right: -40px;
            //     .el-icon-circle-plus-outline:before{
            //         font-size: 30px;
            //     }
            // }
            // .removeCircle{
            //     position: absolute;
            //     margin-top: 10px;
            //     right: -40px;
            //     .el-icon-remove-outline:before{
            //         font-size: 30px;
            //     }
            // }
        }
    }
}
</style>


