</template>
<script>
export default {
    name:'subjectApplyShow',
    data(){
        return{
            showForm:{
                topicName:'',
                topicNumber:'',
                subjectUndertakingUnit:'',
                unitNature:'',
                projectLeader:'',
                projectLeaderPhone:'',
                projectLeaderMail:'',
                postalAddress:'',
                agreementStartTime:'',
                agreementEndTime:'',
                applicationAcceptanceTime:'',
                applicationAcceptanceMode:'',
                applicationAcceptancePlace:'',
                acceptanceContact:'',
                acceptanceContactPhone:'',
                mainContentSituation:'',
                submissionAchievementsSituation:'',
                submitInventory:[],
                subjectUndertakingUnitOpinion:'',
                environmentalDepartmentsOpinion:'',
                provinceAssessmentCenterOpinion:'',
                competentDepartmentOinion:'',
                // applicationAcceptanceUrl:'',
                // achievementsUrl:''
            },
            unitNatureOptions:[{//
                "id": 1,
                "classification": null,
                "content": "",
                "classificationId": null,
                "contentId": null,
                "state": null
            }],
            applicationAcceptanceModeOptions:[{//
                id:'',
                content:''
            }],
            submitInventoryOptions:[{//
                id:'',
                content:''
            }],
            finalAcceptanceMethod:[{//
                id:'',
                content:''
            }],
            paramsData:{
                id:this.$route.params.id,
                arrays:this.$route.params.arrays,
                isShow:this.$route.params.isShow
            },
            form:{
                reason:''
            },
            rules: {//
                reason:[
                    { required: true, message: '', trigger: 'blur' }
                ],
            },
            dialogVisible:false,
            
        }
    },
    methods:{
        // 
        handleExime(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/apply/examine',
                params:{
                    id:_this.paramsData.id[0],
                    type:true
                }
            }).then(function(res){
                if(res.data.resultFlag == 1){
                    _this.$alert('', '', {
                        confirmButtonText: '',
             type: 'warning'
           }).then(() => {
                        _this.$router.push('/');
                    }).catch(() => {
                        _this.$router.push('/');
                    });
                }else{
                    _this.$alert('', '', {
                        confirmButtonText: '',
             type: 'warning'
           }).then(() => {
                        _this.$router.back(0)
                    }).catch(() => {
                        _this.$router.back(0)
                    });
                }
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        handleReject(){
            this.dialogVisible = true
        },
        //
        handleClose(){
            this.dialogVisible = false
        },
        // 
        handleBtnConfirm(formReason){
            let _this = this
            _this.$refs[formReason].validate((valid) => {
                if (valid) {
                   this.axios({
                        method:'POST',
                        url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/apply/examine',
                        params:{
                            type:false,
                            reason:_this.form.reason,
                            id:_this.paramsData.id[0]
                        }
                    }).then(function(res){
                        _this.$alert('', '', {
                            confirmButtonText: '',
                            type:'warning',
                        }).then(() => {
                            _this.$router.back(0)
                        }).catch(() => {
                            _this.$router.back(0)
                        })
                    }).catch(function(err){
                        console.log(err)
                    })
                } else {
                    return false;
                }
            });
        },
        // 
        handleBack(){
            this.$router.back(-1)
        },
        // 
        getUnitNature(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/unitNature',
            }).then(function(res){
                _this.unitNatureOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        //
        getApplicationAcceptanceModeOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationAcceptance',
            }).then(function(res){
                _this.applicationAcceptanceModeOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getSubmitInventoryOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationSubmitList'
            }).then(function(res){
                _this.submitInventoryOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 
        getFinalAcceptanceMethod(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/finalAcceptanceMethod',
            }).then(function(res){
                _this.finalAcceptanceMethod = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // id
        getInfoById(id){
            if(this.paramsData.arrays != '' || this.paramsData.arrays != undefined){
                this.paramsData.arrays.map((item) => {
                    if(id == item.id){
                        this.showForm = item
                        this.showForm.submitInventory = JSON.parse(item.submitInventory)
                        return;
                    }
                })
            }
        }
        
    },
    async mounted(){
       await this.getUnitNature()//
       await this.getApplicationAcceptanceModeOptions()//
       await this.getSubmitInventoryOptions()//
       await this.getFinalAcceptanceMethod()//
       await this.getInfoById(this.paramsData.id)
    }
}
</script>
<style lang="less">
#subjectApplyShow{
    .showForm{
        .el-form{
            .el-table{
                min-width: 900px;
            }
            .form_table1{
                margin-top: 40px;
                width: 59.4%;
                min-width: 900px;
                tr{
                    td{
                        border-left: 1px solid #e0e0e0;
                        border-bottom: 1px solid #e0e0e0;
                        height: 50px;
                        text-align: center;
                        &:last-child{
                            border-right: 1px solid #e0e0e0;
                        }
                    }
                    &:first-child{
                        border-top: 1px solid #e0e0e0;
                        td{
                            background-color:#e5f3ff;
                        }
                    }
                }
            }
            td{
                text-align: left;
            }
            .el-select{
                width: 100%;
            }
            .el-radio{
                line-height: 50px;
                margin: 0 10px;
                .el-radio__input{
                    padding-right: 5px;
                }
                .el-radio__input.is-+span.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked .el-radio__inner{
                    background-color: #0abd90;
                    border-color: #0abd90;
                }
                .el-radio__input.is-.is-checked .el-radio__inner::after{
                    background-color: #fff;
                }
            }
            textarea{
                min-height: 100px !important;
                padding: 0 10px;
            }
            .el-checkbox-group{
                >span{
                    margin:5px 10px;
                    display: inline-block;
                    .el-checkbox{
                        min-width: 120px;
                        
                        .el-checkbox__label{
                            display: inline-block;
                        }
                    } 
                }
            }
            .el-checkbox__label{
                margin-left: 5px;
            }
            #achievementsFile,#applicationAcceptanceFile,#submitInventoryFile{
                margin-left:10px;
            }
            .el-date-editor{
                .el-input__inner{
                    padding-left: 30px;
                }
                .el-input__prefix{
                    line-height: 50px;
                }
            }
        }
    }
    .el-dialog__wrapper{
        .el-dialog{
            margin-top: 0 !important;
        }
        .el-form{
            .el-form-item__label{
                width: 200px !important;
            }
            .el-form-item__content{
                margin-left: 200px !important;
            }
            textarea{
                min-height: 278px !important;
                padding: 10px;
            }
        }
        .el-dialog__footer{
            text-align: center;
        }
    }
}
</style>


